.kv {
 display: flex;
 width: 100%;
 height: 200px;
 justify-content: center;
 align-items: center;
 background: #dddddd;
 font-size: clamp(0.875rem, -0.437rem + 5.6vw, 2.25rem);
}
@media screen and (max-width: 1024px) {
 .kv {
  background: #cccccc;
 }
}
@media screen and (max-width: 768px) {
 .kv {
  background: #bbbbbb;
 }
}
@media screen and (max-width: 600px) {
 .kv {
  background: #aaaaaa;
 }
}

.block1 {
 width: 100%;
 max-width: 1100px;
 margin: 60px auto;
}
.block1 p {
 font-size: var(--fz_lg);
 text-align: left;
 text-align: center;
}